import axios from 'axios'
export default {
  name: 'newTaskForm',
  data () {
    return {
      item: {
        name: '',
        creatorID: parseInt(localStorage.getItem('id')),
        startTime: '',
        endTime: '',
        leaderID: '',
        content: '',
        target: '',
        participants: []
      },
      list: []
    }
  },
  methods: {
    getPIList () {
      axios.get(this.url + '/user/level/' + this.PI)
        .then(this.getPIListSucc)
    },
    getPIListSucc (res) {
      res = res.data
      if (res) {
        this.LeaderList = res
        this.list = this.list.concat(this.LeaderList)
      }
    },
    getPRList () {
      axios.get(this.url + '/user/level/' + this.PR)
        .then(this.getPRListSucc)
    },
    getPRListSucc (res) {
      res = res.data
      if (res) {
        this.FullList = res
        this.list = this.list.concat(this.FullList)
        console.log(this.list)
      }
    },
    getStudentList () {
      axios.get(this.url + '/user/level/' + this.student)
        .then(this.getStudentListSucc)
    },
    getStudentListSucc (res) {
      res = res.data
      if (res) {
        this.StudentList = res
      }
    },
    saveTask () {
      sessionStorage.setItem('task[0]', this.item)
    }
  },
  mounted () {
    this.item.leaderID = -1
    this.getPIList()
    this.getPRList()
    this.getStudentList()
  }
}



